<script>
  import Draco from "draco-vis";
  export let height = 350;
  export let width = 250;

  export let attributesWeight = [];
  let markAttr = [],
    encodingAttr = [];
  let canvas;

  export let recommendations, recommendationsClass;

  /** The following descriptions are provided by IMDB
   *  https://www.imdb.com/interfaces/
   * principals and genre have been modified from the original dataset
   * Their descriptions reflect the modifications
   * */
  let descriptions = {
    type: {
      type: "categorical",
      des: "the type/format of the title (e.g. movie, short, tvseries, tvepisode, video, etc)",
    },
    minutes: {
      type: "quantitative",
      des: "primary runtime of the title, in minutes",
    },
    rating: {
      type: "quantitative",
      des: "weighted average of all the individual user ratings",
    },
    votes: {
      type: "quantitative",
      des: "number of votes the title has received",
    },
    principals: {
      type: "quantitative",
      des: "total principal cast/crew for titles",
    },
    genre: {
      type: "categorical",
      des: "first genre associated with the title",
    },
    categorical: { type: "any", des: "type, genre" },
    quantitative: { type: "any", des: "minutes, rating, votes, principals" },
  };

  let attributesList = [
    "type",
    "minutes",
    "rating",
    "votes",
    "principals",
    "genre",
  ];

  $: {
    let opacityScale = d3
      .scaleLinear()
      .domain(d3.extent(attributesWeight.map((a) => a[1])))
      .range([0.25, 1]);

    let newAttributes = [],
      newMarkAttr = [],
      newEncodingAttr = [];

    for (let a of attributesWeight) {
      let feature = a[0];

      let name, value, opacity;

      if (feature.indexOf("mark") > -1) {
        name = "mark";
        value = feature.slice(feature.indexOf("_") + 1);

        opacity = opacityScale(a[1]);

        newMarkAttr.push([name, value, opacity]);
      } else if (feature.indexOf("bin") > -1) {
        name = feature.slice(0, feature.indexOf("."));
        value = "bin";
        opacity = opacityScale(a[1]);
      } else {
        if (feature.endsWith("field")) {
          continue;
        }

        name = feature.slice(0, feature.indexOf("."));

        value = feature.slice(feature.indexOf("_") + 1, feature.length);
        opacity = opacityScale(a[1]);

        if (value === "nominal") {
          value = "categorical";
        }
        newEncodingAttr.push([name, value, opacity]);
      }
    }

    newMarkAttr = newMarkAttr.sort((a, b) => b[1] > a[1]);
    newEncodingAttr = newEncodingAttr.sort((a, b) => b[1] > a[1]);

    markAttr = newMarkAttr.slice(0, 10);
    encodingAttr = newEncodingAttr.slice(0, 10);
  }

  $: for (let rec = 0; rec < recommendations.length; rec++) {
    if (!recommendations[rec]) {
      continue;
    }
    vegaEmbed(`#vis${rec}`, recommendations[rec]["vega"], {
      actions: false,
    }).then((result) => {
      let canvas = document.querySelector("canvas");
      // Mobile view
      if (window.innerWidth <= 645) {
        canvas.style.width = "100%";
        canvas.style.height = "100%";
      }
      // Desktop view
      else {
        canvas.style.width = "250";
        canvas.style.height = "350";
      }
    });
  }
</script>

<div class="d-flex align-items-center">
  <button class="btn btn-lg round"><i class="fa fa-chevron-left" /></button>
  <div id="recommendationDisplay" class="px-0">
    {#each recommendations as c, i}
      <div id="vis{i}" class="position-relative" {height} {width} />
    {/each}
  </div>

  <button class="btn btn-lg round"><i class="fa fa-chevron-right" /></button>
</div>

<style>
  .round {
    border-radius: 50%;
  }

