import defaultConstraints from './defaultConstraints.js';
import Draco from 'draco-vis';

export async function getRecombinations(vegaSpecs, dataset) {
		let selectedIndices = new Set();
		let allDracoRecommendations = [];
		let count = 1;

		// get count=1 random chart
		while (allDracoRecommendations.length < count) {
			let newIndex = getRandom(0, vegaSpecs.length);
			//console.log(newIndex);
			//let newIndex = 17;
			// test: newIndex = 38 for long heat map
			if (!selectedIndices.has(newIndex)) {
				selectedIndices.add(newIndex);
				let newRecommendations = await getTests(newIndex, vegaSpecs, dataset);
				allDracoRecommendations.push(newRecommendations);
			}
		}
		return allDracoRecommendations;
}

function getRandom(min, max) {
	return Math.floor(Math.random() * (max - min) + min);
}

function getTests(index, vegaSpecs, dataset) {
	// console.log('spec', vegaSpecs[index])
	let encoding = vegaSpecs[index].spec;

	// let encoding = {'label': 1, 'mark_bar': 1, 'y.type_quantitative': 1, 'y.field_minutes': 1, 'y.field_rating': 0, 'y.field_votes': 0, 'y.bin_True': 1, 'x.type_quantitative': 1, 'x.aggregate_count': 1}
	// let encoding = {'label': 1, 'mark_line': 1, 'x.type_quantitative': 1, 'x.field_minutes': 0, 'x.field_rating': 1, 'x.bin_True': 1, 'y.type_quantitative': 1, 'y.aggregate_count': 1}

	let constraints = [];
	for (let attr of Object.keys(encoding)) {
		if (attr.includes("filename")) { continue; }
		else if (attr.includes("none")) { continue; }
		else if (encoding[attr] !== 0) {
			constraints.push({ 'attr': attr, 'value': encoding[attr] });
		}
	}

	return new Promise((resolve, reject) => {
		resolve(solveDraco(constraints, dataset));
	})
}

function solveDraco(newConstraints, dataset) {
	// console.log(newConstraints)
	let recs = [];

	const url = 'https://unpkg.com/wasm-clingo@0.2.2'
	let markConstraints = dracoMarkConstraints(newConstraints);
	let visConstraints = dracoVisConstraints(newConstraints);
	// let visConstraints = ''

	const draco = new Draco(url);
	return draco.init().then(() => {
		// Get metadata about dataset
		draco.prepareData(dataset);
		const schema = draco.getSchema();
		const dataConstraints = dracoDataConstraints(schema);

		// Create constraints based on schema
		const inputConstraints = `
			data("movies.csv").
			num_rows(33778).

			${dataConstraints}

			${markConstraints}

			% ====== Query constraints ======
			${visConstraints}
		`;

		//console.log(inputConstraints);

		const solution = draco.solve(inputConstraints, { models: 1 });

		if (!solution) {
			return [];
		}

		for (let [i, s] of solution['specs'].entries()) {
			s.width = 350;
			s.height = 450;
			recs.push({ 'vega': s, 'uid': 'id' + (new Date().valueOf()) + '_' + i });
		}

		return recs;
	})
}

export function dracoMarkConstraints(constraints) {
	if (!constraints) { return '' }
	let defaults = defaultConstraints['defaultMarkConstraints'];

	let allConstraints = [];

	// Select constraints that begin with 'mark' have a value of 1
	//contraint -> 'mark_bar': 1, 'y.type_quantitative': 1
	for (let contraint of constraints) {
		let attr = contraint['attr'];
		if (attr.startsWith('mark')) {
			let markType = attr.slice(5);
			let value = contraint['value'];
			if (value > 0) {
				allConstraints.push(`mark(${markType}).`)
			}
		}
	}

	if (allConstraints.length > 0) {
		return allConstraints.join('\n\n');
	}

	return '';
}

export function dracoVisConstraints(constraints) {
	if (!constraints) { return '' }
	let channels = ['x', 'y', 'row', 'column', 'color', 'size', 'shape', 'text'];

	let encodingCount = 0;
	let allConstraints = [];

	for (let channel of channels) {
		let relevant = constraints.filter(function (a) { return a['attr'].startsWith(channel) });
		if (relevant.length != 0) {

			let newConstraint = `encoding(e${encodingCount}).:- not channel(e${encodingCount}, ${channel}).`;
			for (let r of relevant) {
				let attr = r['attr'];
				// If there are datatype preferences
				if (attr.includes('.type')) {
					// Get type
					let type = attr.slice(attr.indexOf('type') + 5);
					if (r['value'] > 0) {
						// 'moreLikeThis' type
						newConstraint = newConstraint + `:- not type(e${encodingCount}, ${type}).`;
					}
				}

				// If there are field preferences
				if (attr.includes('.field')) {
					// Get field
					let field = attr.slice(attr.indexOf('field') + 6);
					if (field === '') { }
					else if (r['value'] > 0) {
						// 'moreLikeThis' field
						newConstraint = newConstraint + `:- not field(e${encodingCount}, ${field}).`;
					} else {
						// 'lessLikeThis' field
						newConstraint = newConstraint + `:- field(e${encodingCount}, ${field}).`;
					}

				}

				// If there are aggregate preferences
				if (attr.includes('.aggregate')) {

					// Get aggregate
					let agg = attr.slice(attr.indexOf('aggregate') + 10);

					if (r['value'] > 0) {
						// 'moreLikeThis' field
						newConstraint = newConstraint + `:- not aggregate(e${encodingCount}, ${agg}).`;
					}
				}

				// If there are binning preferences
				if (attr.includes('.bin')) {

					if (r['value'] > 0) {
						// 'moreLikeThis' bin
						newConstraint = newConstraint + `:- not bin(e${encodingCount}, 10).`;
					}
				}

			}

			allConstraints.push(newConstraint);
			encodingCount++;
		}

	}

	if (allConstraints.length > 0) {
		return allConstraints.join('\n\n');
	}

	return '';
}

export function dracoDataConstraints(schema) {
	//console.log("schema", schema);
	let allConstraints = [];
	let attributes = Object.keys(schema['stats']);

	for (let a of attributes) {
		let attributeSchema = schema['stats'][a];
		let attributeConstraint = schemaToConstraint(a, attributeSchema);
		allConstraints.push(attributeConstraint);
	}

	return allConstraints.join('\n\n');
}

function schemaToConstraint(a, attributeSchema) {
	//console.log("attributeSchema", attributeSchema);
	let attributeType = getType(Object.keys(attributeSchema['unique']));
	//console.log("attributeType", attributeType);


	let constraintTemplate = `fieldtype(${a},${attributeType}). cardinality(${a},${attributeSchema['distinct']}).`;

	return constraintTemplate;
}

// This is a heuristic function and may not always work
// May need to be improved at a later date
function getType(uniqueValues) {
	//console.log("uniqueValues", uniqueValues);
	let result = 'number';

	for (let v of uniqueValues) {

		// Does Draco support ordinal/temporal?
		// Needs verification
		if (isNaN(parseFloat(v)) && v != "NaN") {
			result = 'string';
		} else {
			continue;
		}
	}

	// console.log(result)

	return result;
}