<script>
  import * as recommendationHelpers from "./recommendationHelpers.js";
  import Visualizations from "./Visualizations.svelte";
  export let dataset;
  /* let selectedAttributes;
  let participant;
  let allParticipantInfo; */

  let recommendationCount = 1;
  let attributesWeight = [];
  let sessionData = [];
  // Current recommendations
  let recommendations = [];
  // Recommendations generated from the same draco query
  let similarRecommendations = [];
  // Recommendations class
  let recommendationsClass = Array(recommendationCount).fill("default");

  // true if updating recommendations from server
  let loading = false;

  let recsPromise = showInitialRecommendations();
  

  async function showInitialRecommendations() {
    let vegaSpecs = await loadSpecs();
    let recs = await recommendationHelpers.getRecombinations(vegaSpecs, dataset);
    return selectRecommendations(recs);
  }

  function selectRecommendations(recommendationSets) {
    similarRecommendations = recommendationSets;

    let result = [];
    let resultUid = new Set();
    let setNumber = 0;

    while (result.length < 1) {
      let set = similarRecommendations[setNumber % 1];
      if (set.length == 0) {
        setNumber++;
        continue;
      }

      let selectedUid;
      let selected;

      while (!selectedUid || resultUid.has(selectedUid)) {
        let randomIndex = Math.floor(Math.random() * set.length);
        selected = set[randomIndex];
        selectedUid = selected.uid;
      }

      result.push(selected);
      resultUid.add(selected.uid);

      setNumber++;
    }

    // console.log(resultUid)

    recommendationsClass = recommendationsClass.map((r) => "default");
    recommendations = result;

    sessionData.push({
      constraints: attributesWeight,
      recommendations: recommendations,
      date: new Date(),
      label: "recommendations",
    });

    loading = false;
    return recommendations;
  }

  // put these in processing file, put datasets
  async function loadSpecs() {
    const specs = await d3.csv(
      `datasets/manual_specs_one_hot_encoding_4.csv`,
      dataPreprocessor
    );
    let vegaSpecsOriginal = [];
    for (let i in specs) {
      if (i === "columns") {
        continue;
      }
      let s = specs[i];
      delete s.index;
      vegaSpecsOriginal.push({ spec: s });
    }
    // convert directly from string?
    return JSON.parse(JSON.stringify(vegaSpecsOriginal));
  }

  function dataPreprocessor(d) {
    let result = {};
    for (let i = 0; i < Object.keys(d).length; i++) {
      let variableName = Object.keys(d)[i];
      result[variableName] = +Object.values(d)[i];
    }
    return result;
  }
</script>

<div>
  {#await recsPromise}
    <p>...loading</p>
  {:then recs}
    <Visualizations
      {attributesWeight}
      recommendations={recs}
      {recommendationsClass}
    />
  {:catch error}
    <p style="color: red">{error.message}</p>
  {/await}
</div>
