  let promise = loadData();

  async function loadData() {
    return await d3.csv(`movies.csv`, dataPreprocessor);
  }

  // Preprocess movies dataset
  function dataPreprocessor(d) {
    let result = {};
    for (let i = 0; i < Object.keys(d).length; i++) {
      let variableName = Object.keys(d)[i];
      if (
        variableName === "minutes" ||
        variableName === "rating" ||
        variableName === "votes" ||
        variableName === "principle"
      ) {
        result[variableName] = +Object.values(d)[i];
      } else result[variableName] = Object.values(d)[i];
    }
    return result;
  }

